import { BrowserPopoverOption } from './../../../models/browser';
import { Component } from '@angular/core';
import {IonicPage, ViewController, NavParams } from "ionic-angular";

@IonicPage()
@Component({
  selector: 'page-browser-popover',
  templateUrl: 'browser-popover.html'
})
export class BrowserPopoverPage {
  parentCallback: {refresh: () => void, share?: () => void, copyurl: () => void, close: () => void,  openInBrowser?: () => void
    shareWxTimeLine?: () => void, shareWxSession?: () => void, openComplaint?: () => void};
  isShare: false; 
  options: BrowserPopoverOption;
  constructor(public viewCtrl: ViewController,
              private navParams: NavParams) {
    this.parentCallback = this.navParams.data.callback;
    this.isShare = this.navParams.data.isShare;
    this.options = this.navParams.data.options;
    
  }

  openComplaint(){
    this.parentCallback.openComplaint();
    this.viewCtrl.dismiss();
  }

  openInBrowser(){
    this.parentCallback.openInBrowser();
    this.viewCtrl.dismiss();
  }
  // 刷新
  refresh() {
    this.parentCallback.refresh();
    this.viewCtrl.dismiss();
  }

  // 分享
  share() {
    this.parentCallback.share();
    this.viewCtrl.dismiss();
  }
  // 分享到微信好友
  shareWxSession() {
    this.parentCallback.shareWxSession();
    this.viewCtrl.dismiss();
  }
  // 分享到微信朋友圈
  shareWxTimeLine() {
    this.parentCallback.shareWxTimeLine();
    this.viewCtrl.dismiss();
  }
  // 複製鏈接
  copyurl() {
    this.parentCallback.copyurl();
    this.viewCtrl.dismiss();
  }
  // 关闭
  close(){
    this.viewCtrl.dismiss();
    this.parentCallback.close();
  }
}


